import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import {CSSTransition} from "react-transition-group";
import { actionCreators } from './store';
import { actionCreators as loginActionCreators } from '../../pages/login/store';
import {HeaderWrapper, Logo, Nav, NavItem, NavSearch, Addition, Button,
    SearchWrapper, SearchInfo, SearchInfoTitle, SearchInfoSwitch, SearchInfoItem} from "./style";

class Header extends PureComponent{

    getListArea(){
        const { focused, list, page, mouseIn, totalPage, handleMouseEnter, handleMouseLeave, handleChangePage } = this.props;
        const newList = list.toJS();
        const pageList = [];

        if (newList.length){
            for (let i = (page-1) * 10; i < page * 10;i++){
                if (newList[i]){
                    pageList.push(
                        <SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
                    )
                }
            }
        }
        if (focused || mouseIn) {
            return(
                <SearchInfo
                    onMouseEnter={handleMouseEnter}
                    onMouseLeave={handleMouseLeave}>
                    <SearchInfoTitle>
                        
                        <SearchInfoSwitch onClick={()=>handleChangePage(page, totalPage, this.spinIcon)}>
                            <span ref={(icon) => {this.spinIcon = icon}} className="iconfont spin">&#xe615;</span>
                            
                        </SearchInfoSwitch>
                    </SearchInfoTitle>
                    {pageList}
                </SearchInfo>
            )
        }else {
            return null;
        }
    }
    render() {
        const { focused, list, handleInputFocus, handleInputBlur, login, logout } = this.props;
        return(
            <HeaderWrapper>
                <Link to='/'>
                    <Logo />
                </Link>
                <Nav>
                    <NavItem className='left active'></NavItem>
                    <NavItem className='left'>App</NavItem>
                    { login ?
                        <NavItem onClick={logout} className='right'></NavItem> :
                        <Link to='/login'><NavItem className='right'></NavItem></Link>}
                    <NavItem className='right'>
                        <span className="iconfont">&#xe636;</span>
                    </NavItem>
                    <SearchWrapper>
                        <CSSTransition
                            in={focused}
                            timeout={800}
                            classNmaes="slide">
                            <NavSearch
                                className={focused ? 'focused': ''}
                                onFocus={() => handleInputFocus(list)}
                                onBlur={handleInputBlur}
                            ></NavSearch>
                        </CSSTransition>
                        <span className={focused ? 'focused iconfont zoom': 'iconfont zoom'}>&#xe637;</span>
                        {this.getListArea()}
                    </SearchWrapper>
                </Nav>
                <Addition>
                    <Link to='/write'>
                        <Button className='write'>
                            <span className="iconfont">&#xe600;</span>
                            
                        </Button>
                    </Link>
                    <Button className='reg'></Button>
                </Addition>
            </HeaderWrapper>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        focused:state.getIn(['header','focused']),
            //state.get('header').get('focused')
        list:state.getIn(['header', 'list']),
        page:state.getIn(['header', 'page']),
        mouseIn:state.getIn(['header', 'mouseIn']),
        totalPage:state.getIn(['header', 'totalPage']),
        login: state.getIn(['login', 'login'])
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleInputFocus(list){
            (list.size === 0) && (dispatch(actionCreators.getList()));
            dispatch(actionCreators.searchFocus());
        },
        handleInputBlur(){
            dispatch(actionCreators.searchBlur());
        },
        handleMouseEnter(){
            dispatch(actionCreators.mouseEnter());
        },
        handleMouseLeave(){
            dispatch(actionCreators.mouseLeave());
        },
        handleChangePage(page, totalPage, spin){
            spin.style.transform = spin.style.transform + 'rotate(360deg)';
            if (page < totalPage ){
                dispatch(actionCreators.changePage(page + 1));
            }else{
                dispatch(actionCreators.changePage(1));
            }
        },
        logout(){
            dispatch(loginActionCreators.logout());
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);